<template>
  <!-- 地下水監測站頁面 -->
  <div class="environment">
    <!-- 地下水監測站專區 -->
    <div class="row" style="margin: 0 15px 0 15px">
      <div class="col-12 text-center">
        <span
          class="badge badge-pill badge-info animate__animated animate__fadeInUp text-center title"
          style="animation-duration: 1s; animation-iteration-count: 1"
          >地下水監測站</span
        >
      </div>
    </div>
    <!-- 地下水監測相關介紹 -->
    <div class="childtitle one">你知道嗎?</div>
    <div class="content">
      <el-row class="explainRow" :gutter="20">
        <!-- 地下水介紹 -->
        <el-col :xs="24" :span="12">
          <div class="explainTitle one">地下水小知識</div>
          <div class="subtitle">
            地下水共分為兩類，且監測的標準也不一。 <br />
            <b>· 第一類：</b>飲用水水源水質保護區內之地下水<br />
            <b>· 第二類：</b>第一類以外之地下水 <br />
          </div>
          <div class="subtitle">
            地下水的品質控管又分為<span class="explain_heilight2"> 監測 </span
            >與<span class="explain_heilight2"> 管制 </span>。<br />
            <b>· 地下水污染監測：</b>指基於<span class="explain_heilight2">
              地下水污染預防目的</span
            >。<br />
            <b>· 地下水污染管制：</b>指為<span class="explain_heilight2">
              防止地下水污染惡化</span
            >。
          </div>
          <div class="explainTitle two">台灣自來水與地下水現況</div>
          <div class="subtitle">
            調查顯示自民國107年1月 至 108年7月，
            在台灣各縣市的自來水水質評比中，中南部等縣市的自來水水質仍然不好，<span
              class="explain_heilight1"
              >其中最差的屬於高雄市</span
            >
            且自來水水質差的地區主要為<span class="explain_heilight1"
              >抽取地下水導致</span
            >。
          </div>
          <!-- 資料來源 -->
          <div class="infoFrom">
            資料來源：<a href="https://www.nownews.com/news/3648561"
              >全國自來水評比出爐！新北排名第一 高雄水質最差</a
            >
          </div>
        </el-col>
        <!-- 地下水監測標準表格 -->
        <el-col :xs="24" :span="12">
          <div class="subtitle">
            <b>下表為依據背景與指標水質項目，第一類及第二類地下水的監測標準</b>
          </div>
          <el-table
            :data="ExplanTable"
            style="width: 100%"
            :header-cell-style="tableHeaderColor"
            :row-style="{ background: '#FFFFF0' }"
          >
            <el-table-column prop="item" label="監測項目"> </el-table-column>
            <el-table-column prop="firstStandard" label="第一類監測標準值">
            </el-table-column>
            <el-table-column prop="secondStandard" label="第二類監測標準值">
            </el-table-column>
          </el-table>
          <div class="website">
            ※ 更多地下水管制相關標準詳情請洽：
            <a href="https://sgw.epa.gov.tw/public/misc/service/pollutant"
              >行政院環境保護署地下水污染物監測、管制標準</a
            >
          </div>
        </el-col>
      </el-row>
    </div>
    <!-- 高雄地下水監測站位置及最新檢測資訊 -->
    <hr class="hr" />
    <div class="childtitle two">高雄地下水監測站位置及最新檢測資訊</div>
    <div class="content">
      <div>
        下方為高雄所有地下水監測站位置 及 最新的檢測資訊<br />
        Tip : 點擊左方行政區即可查看監測該行政區的監測站位置及最新的檢測資訊
      </div>
      <el-row :gutter="10">
        <el-col :xs="24" :span="12">
          <!-- 高雄行政區地圖 -->
          <div id="mapchart"></div>
        </el-col>
        <el-col :xs="24" :span="12">
          <!-- 地下水監測站地圖 -->
          <div class="searchDisName">
            {{ clickDisName }}
          </div>
          <l-map class="map" :zoom="zoom" :options="options" :center="center">
            <!-- 載入地圖資料 -->
            <l-tile-layer :url="url" :attribution="attribution" />
            <!-- 地標 -->
            <l-marker
              ref="location"
              :lat-lng="item.local"
              v-for="item in stationlatlng"
              :key="item.name"
            >
              <!-- 地標圖示 -->
              <l-icon :icon-url="iconimg" />
              <l-tooltip :options="tooltipOptions">{{ item.name }}</l-tooltip>
              <!-- 點擊圖標會彈出顯示的文字 -->
              <l-popup>{{ item.address }}</l-popup>
            </l-marker>
          </l-map>
        </el-col>
      </el-row>
      <!-- 點擊的行政區所有檢測站的最新檢測資料 -->
      <div v-loading="loading" element-loading-text="等待政府資料中...">
        <div v-if="newestData.length !== 0">
          <!-- 顏色對照檢測是否通過說明 -->
          <el-row class="colorExplain">
            <el-col :xs="3" :span="5" style="padding: 0px 2% 0px 0px">
              <div class="passBlock"></div>
            </el-col>
            <el-col :xs="9" :span="7">
              <span>通過監測標準</span>
            </el-col>
            <el-col :xs="3" :span="5" style="padding: 0px 2% 0px 0px">
              <div class="unpassBlock"></div>
            </el-col>
            <el-col :xs="9" :span="7">
              <span>未通過監測標準</span>
            </el-col>
          </el-row>
          <!-- 表格資訊 -->
          <div v-for="index in 2" :key="index">
            <div class="searchTitle">
              {{ setSearchTitle(index) }}
            </div>
            <div v-for="item in newestData" :key="item.name">
              <div class="newestDataName">
                <span>測站名稱：{{ item.name }}</span>
                <span style="margin-left: 20px"
                  >檢測日期 : {{ item.date }}</span
                >
              </div>
              <el-table
                :data="[item]"
                style="width: 100%"
                :header-cell-style="{
                  background: '#5F5F5F',
                  color: 'white',
                  fontSize: '18px',
                }"
                :cell-style="index === 1 ? tableCellStyle : tableCellStyle2"
              >
                <el-table-column prop="item1" label="鐵"> </el-table-column>
                <el-table-column prop="item2" label="錳"> </el-table-column>
                <el-table-column prop="item3" label="總硬度"> </el-table-column>
                <el-table-column prop="item4" label="總溶解固體物">
                </el-table-column>
                <el-table-column prop="item5" label="氯鹽"> </el-table-column>
                <el-table-column prop="item6" label="氨氮"> </el-table-column>
                <el-table-column prop="item7" label="硫酸鹽"> </el-table-column>
                <el-table-column prop="item8" label="總有機碳">
                </el-table-column>
                <el-table-column prop="item9" label="總酚"> </el-table-column>
              </el-table>
            </div>
          </div>
        </div>
      </div>
      <!-- 無資料顯示字樣 -->
      <div
        v-if="newestData.length === 0 && loading === false"
        class="noNewestData"
      >
        目前無{{ clickDisName }}最新的檢測資訊
      </div>
    </div>
    <!-- 檢測資訊進階搜尋 -->
    <hr class="hr" />
    <div class="childtitle three">檢測資訊進階搜尋</div>
    <div>
      <b>下方提供查詢特定高雄的地下水監測站的歷史檢測資訊</b><br />
      Tip :
      選擇欲查詢的行政區及監測站，即可查看該監測站在政府資料中紀錄的歷史檢測資訊
      <br />※ 附註 : 以下的數值為該年的<b>「 平均值 」</b> <br />※ 附註 :
      以下提供「第一類」及「第二類」地下水監測標準值的結果
    </div>
    <el-row class="detailSearchRow">
      <el-col :xs="10" :span="9">
        <b>行政區：</b>
        <el-select
          v-model="search.dis"
          @change="searchStation"
          placeholder="行政區"
        >
          <el-option
            v-for="item in mapOptions.series[0].data"
            :key="item.name"
            :label="item.name"
            :value="item.name"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :xs="10" :span="9">
        <b>監測站：</b>
        <el-select v-model="search.station" placeholder="監測站">
          <el-option
            v-for="item in stationArray"
            :key="item"
            :label="item"
            :value="item"
          >
          </el-option>
        </el-select>
      </el-col>
      <el-col :xs="4" :span="6">
        <el-button @click="searchData">搜尋</el-button>
      </el-col>
    </el-row>
    <div v-loading="loading2" element-loading-text="等待政府資料中...">
      <div v-if="historyData.length">
        <!-- 顏色對照檢測是否通過說明 -->
        <el-row class="colorExplain">
          <el-col :xs="3" :span="5" style="padding: 0px 2% 0px 0px">
            <div class="passBlock"></div>
          </el-col>
          <el-col :xs="9" :span="7">
            <span>通過監測標準</span>
          </el-col>
          <el-col :xs="3" :span="5" style="padding: 0px 2% 0px 0px">
            <div class="unpassBlock"></div>
          </el-col>
          <el-col :xs="9" :span="7">
            <span>未通過監測標準</span>
          </el-col>
        </el-row>
        <div v-for="index in 2" :key="index">
          <div class="searchTitle">{{ setSearchTitle(index) }}</div>
          <el-table
            :data="historyData"
            style="width: 100%"
            :header-cell-style="{
              background: '#5F5F5F',
              color: 'white',
              fontSize: '18px',
            }"
            :cell-style="index === 1 ? tableCellStyle : tableCellStyle2"
          >
            <el-table-column prop="year" label="年份"> </el-table-column>
            <el-table-column prop="item1" label="鐵"> </el-table-column>
            <el-table-column prop="item2" label="錳"> </el-table-column>
            <el-table-column prop="item3" label="總硬度"> </el-table-column>
            <el-table-column prop="item4" label="總溶解固體物">
            </el-table-column>
            <el-table-column prop="item5" label="氯鹽"> </el-table-column>
            <el-table-column prop="item6" label="氨氮"> </el-table-column>
            <el-table-column prop="item7" label="硫酸鹽"> </el-table-column>
            <el-table-column prop="item8" label="總有機碳"> </el-table-column>
            <el-table-column prop="item9" label="總酚"> </el-table-column>
          </el-table>
        </div>
      </div>
    </div>
    <div class="footer"></div>
  </div>
</template>
<script>
import "echarts/lib/component/visualMap";
let echarts = require("echarts/lib/echarts");
let ntpJson = require("@/assets/geo/test.json");
echarts.registerMap("ntp", ntpJson);

export default {
  async mounted() {
    // 行政區地圖初始化及設置
    this.mapchart = echarts.init(document.getElementById("mapchart"));
    this.mapchart.setOption(this.mapOptions);
    this.mapchart.on("click", (e) => this.clickDistrict(e)); // 註冊點擊地圖事件
    window.onresize = () => this.mapchart.resize();
    this.getStationName();
    this.getStationNewestData("鳳山區"); // 預設先找鳳山區的最新地下水監測資訊
  },
  data() {
    return {
      // 地下水監測表格介紹資料
      ExplanTable: [
        {
          item: "鐵 (Fe)",
          firstStandard: "0.15",
          secondStandard: "1.50",
        },
        {
          item: "錳 (Mn)",
          firstStandard: "0.025",
          secondStandard: "0.250",
        },
        {
          item: "總硬度 (以CaCO3計) ",
          firstStandard: "150",
          secondStandard: "750",
        },
        {
          item: "總溶解固體物 (Total dissolved solid)",
          firstStandard: "250",
          secondStandard: "1250",
        },
        {
          item: "氯鹽 (Chloride as Cl-)",
          firstStandard: "125",
          secondStandard: "625",
        },
        {
          item: "氨氮 (Ammonium nitrogen)",
          firstStandard: "0.050",
          secondStandard: "0.25",
        },
        {
          item: "硫酸鹽 (以SO42-計) ",
          firstStandard: "125",
          secondStandard: "625",
        },
        {
          item: "總有機碳 (Total organic carbon)",
          firstStandard: "2.0",
          secondStandard: "10",
        },
        {
          item: "總酚 (Phenols)",
          firstStandard: "0.014",
          secondStandard: "0.14",
        },
      ],
      //行政區地圖設定
      mapOptions: {
        visualMap: {
          left: "70%",
          top: "30%",
          min: 0,
          max: 8,
          splitNumber: 4,
          inRange: {
            color: ["#91c7ae", "#61a0a8", "#DDAA00", "#d48265"],
          },
          text: ["測站多", "測站少"],
          show: true,
        },
        series: [
          {
            type: "map",
            map: "ntp",
            zoom: 3,
            left: "-15%",
            top: "35%",
            aspectScale: 1, // 使地圖不變形
            center: [120.285830852, 22.670955631],
            data: [],
          },
        ],
      },
      // 進階搜尋的綁定值
      search: {
        station: "",
        dis: "",
      },
      mapchart: {}, // 行政區地圖
      historyData: [], // 進階搜尋資料
      stationArray: [], // 進階搜尋的監測站選項
      clickDisName: "鳳山區", // 目前查看最新資料的行政區
      newestData: [], // 目前的最新資料
      stationlatlng: [], // 最新資訊地圖的測站打點
      stationlatlng_original: [], // 最新資訊地圖的所有測站資訊
      loading: true, // 地下水監測站最新資訊loading
      loading2: false, // 檢測資訊進階搜尋loading
      // ---------------------地圖資料---------------------
      //地圖縮放比例
      zoom: 11,
      //地圖中心
      center: [22.612786, 120.442959],
      // 地標圖示
      iconimg: require("leaflet/dist/images/marker-icon.png"),
      //地圖使用網址
      url: "https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png",
      //地圖來源聲明
      attribution: `© <a href="http://osm.org/copyright">OpenStreetMap</a> contributors`,
      // 取消顯示地圖左上角縮放地圖的按鈕
      options: {
        zoomControl: false,
      },
      //tooltip彈框設定
      tooltipOptions: {
        permanent: true,
        offset: [10, 10],
      },
    };
  },
  methods: {
    // 進階搜尋檢測標準類型標題設置
    setSearchTitle(index) {
      if (index === 1) {
        return "第一類地下水監測標準值";
      } else {
        return "第二類地下水監測標準值";
      }
    },
    // 地下水檢測介紹table的header顏色設定
    tableHeaderColor({ columnIndex }) {
      if (columnIndex === 0) {
        return "background-color: #DDAA00;color: #fff;font-weight: 700;";
      }
      if (columnIndex === 1) {
        return "background-color: #91c7ae;color: #fff;font-weight: 700;";
      }
      if (columnIndex === 2) {
        return "background-color: #61a0a8;color: #fff;font-weight: 700;";
      }
    },
    // 最新地下水檢測 & 進階搜尋資料表格style設定(依據第一類檢測標準)
    tableCellStyle({ row, columnIndex }) {
      let cellStyle;
      if (columnIndex === 0 && row.year !== undefined) {
        return (cellStyle = "background: #fffff0");
      }
      let index = 0;
      if (row.year !== undefined) {
        index = columnIndex;
      } else {
        index = columnIndex + 1;
      }
      let firstWWord = row[`item${index}`].substring(0, 1);
      let value = 0;
      value = parseFloat(row[`item${index}`]);
      if (firstWWord === "<" || firstWWord === ">") {
        value = parseFloat(row[`item${index}`].substr(1));
      }
      let standard = [0.15, 0.025, 150, 250, 125, 0.05, 125, 2.0, 0.014];
      if (value < standard[index - 1]) {
        cellStyle = "background: #99DCF1";
      } else if (isNaN(value)) {
        cellStyle = "background: white";
      } else {
        cellStyle = "background: #FBD8D8";
      }
      return cellStyle;
    },
    // 進階搜尋資料表格style設定(依據第二類檢測標準)
    tableCellStyle2({ row, columnIndex }) {
      let cellStyle;
      if (columnIndex === 0 && row.year !== undefined) {
        return (cellStyle = "background: #fffff0");
      }
      let index = 0;
      if (row.year !== undefined) {
        index = columnIndex;
      } else {
        index = columnIndex + 1;
      }
      let firstWWord = row[`item${index}`].substring(0, 1);
      let value = 0;
      value = parseFloat(row[`item${index}`]);
      if (firstWWord === "<" || firstWWord === ">") {
        value = parseFloat(row[`item${index}`].substr(1));
      }
      let standard = [1.5, 0.25, 750, 1250, 625, 0.25, 625, 10, 0.14];
      if (value < standard[index - 1]) {
        cellStyle = "background: #99DCF1";
      } else if (isNaN(value)) {
        cellStyle = "background: white";
      } else {
        cellStyle = "background: #FBD8D8";
      }
      return cellStyle;
    },
    // 進階搜選擇行政區後，找尋該行政區所有測站更新下拉選項
    searchStation() {
      this.stationArray = [];
      let station = this.stationlatlng_original.filter((item) => {
        return item.dis === this.search.dis;
      });
      station.forEach((item) => {
        this.stationArray.push(item.name);
      });
      console.log(station);
    },
    //點擊地圖的行政區
    async clickDistrict(e) {
      let clickDis = this.mapOptions.series[0].data.filter((data) => {
        return data.index === e.dataIndex;
      });
      console.log(clickDis);
      if (clickDis.length === 0) {
        this.$message({
          message: "該行政區無地下水監測站",
          type: "warning",
        });
        return;
      }
      this.stationlatlng = this.stationlatlng_original.filter((item) => {
        return item.dis === clickDis[0].name;
      });
      this.clickDisName = clickDis[0].name;
      this.center = this.stationlatlng[0].local;
      this.getStationNewestData(clickDis[0].name);
    },
    // 取得測站位置資料
    getStationName() {
      let data = require("@/file/groundWater_station.json");
      this.mapOptions.series[0].data = data[0][0];
      console.log(this.mapOptions);
      console.log(data[0]);
      this.mapchart.setOption(this.mapOptions);
      for (let i = 1; i < data[0].length; i++) {
        let mapdetail = {
          name: "",
          address: "",
          dis: "",
          local: [],
        };
        mapdetail.name = data[0][i].SiteName;
        mapdetail.dis = data[0][i].dis;
        mapdetail.address = "地址 : " + data[0][i].SiteAddress;
        mapdetail.local[0] = data[0][i].latitude;
        mapdetail.local[1] = data[0][i].longitude;
        this.stationlatlng_original.push(mapdetail);
      }
      console.log(this.stationlatlng_original);
      this.stationlatlng = this.stationlatlng_original.filter((item) => {
        return item.dis === "鳳山區";
      });
      this.center = this.stationlatlng[0].local;
    },
    // 取得檢測資訊
    getStationNewestData(disName) {
      this.loading = true;
      let data = JSON.parse(
        JSON.stringify(require("@/file/groundWater_newData.json"))
      );
      data = data.filter((item) => {
        return item[0].disName === disName;
      });
      this.newestData = data[0];
      if (this.newestData === undefined) {
        this.newestData = [];
      }
      console.log(this.newestData);
      this.loading = false;
    },
    // 進階搜尋
    searchData() {
      if (this.search.dis === "" || this.search.station === "") {
        this.$message({
          message: "請選擇欲查詢的行政區及監測站",
          type: "warning",
        });
        return;
      }
      console.log(this.search.dis);
      console.log(this.search.station);
      this.loading2 = true;
      this.historyData = [];
      let searchData = [];
      let data = JSON.parse(
        JSON.stringify(require("@/file/groundWater_detailSearch.json"))
      );
      let detail = data.filter((item) => {
        return item[1].siteName === this.search.station;
      });
      searchData.push(detail);
      searchData = searchData.sort(function (x, y) {
        return x[0] - y[0];
      });
      console.log(searchData);
      searchData[0].forEach((item) => {
        item[1].year = item[0];
        item.splice(0, 1); // 刪除年份資料
        this.historyData.push(...item);
      });
      console.log(this.historyData);
      this.loading2 = false;
    },
    download(res) {
      if (!res) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([res]));
      let link = document.createElement("a");
